@import "src/colors";

$btn-round-size: 40px;

.section:nth-child(even){
  background-color: $custom-white-dark;
}

.panel {
  height: 100%;
  display: grid;
  grid-template-rows: 100%;
  grid-template-columns: 50% 50%;

  .panel-left {
    align-self: center;
    justify-self: center;
  }

  .panel-img-left {
    width: 100%;
    height: 100%;
    object-fit: cover;
    justify-self: start;
  }

  .panel-right {
    align-self: center;
    justify-self: center;
  }

  .panel-img-right {
    width: 100%;
    height: 100%;
    object-fit: cover;
    justify-self: end;
  }
}

.img-cut-left {
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 25% 100%);
  clip-path: polygon(0 0, 100% 0, 100% 100%, 25% 100%);
}

.img-cut-right {
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 25% 100%);
  clip-path: polygon(0 0, 75% 0, 100% 100%, 0% 100%);
}

.icon-list {
  .item {
    display: flex;
    align-items: center;
    padding: 4px;

    i {
      margin-right: 8px;
    }

    p {
      margin-bottom: 0;
    }
  }
}

.btn-round-row {
  display: flex;
  flex-wrap: wrap;
  width: 70%;

  margin: 0 -6px;

  a {

    margin: 6px 6px;
  }
}

.btn-round {
  border-radius: 50%;
  width: $btn-round-size;
  height: $btn-round-size;

  display: flex;
  justify-content: center;
  align-items: center;
}

.skills {
  display: flex;
  flex-wrap: wrap;
  width: 70%;

  margin: 0 -6px;

  .item {
    border-radius: .25rem;
    padding: 2px 8px;
    color: $custom-white;
    background-color: $custom-gray;

    margin: 6px 6px;
  }
}
